export default class BridgeParam {

    /**
     *   json string  .
     *
     * @param clearEmpty - true   (  )  key . (default: false)
     * @returns {string}
     */
    toString(clearEmpty) {
        if(clearEmpty) {
            return JSON.stringify(this, (key, value) => {
                if(!value) {
                    return undefined;
                } else {
                    return value;
                }
            });
        } else {
            return JSON.stringify(this);
        }
    }
}



